export default {
	data() {
		return {

		}
	},
	onLoad() {
		// this.getNetwork()
		// this.getNetworkType()
		
	},
	mounted() {
		this.getNetwork()
	},
	onReady() {
		this.getNetwork()
	},
	methods: {
		// 获取当前网络
		getNetworkType() {
			my.getNetworkType({
				success: (res) => {
					console.log("当前网络:", JSON.stringify(res))
					let {
						isConnected,
						networkType
					} = res
					console.log(networkType)
					if (networkType === "none") {
						uni.showModal({
							title: '提示',
							content: '网络丢失，请检查网络是否正常',
							success: function(res) {
								if (res.confirm) {
									console.log('用户点击确定');
								} else if (res.cancel) {
									console.log('用户点击取消');

								}
							}
						});
					}
				}
			})
		},
		// 监听网络状态
		getNetwork() {
			my.onNetworkStatusChange(res => {
				console.log("网络状态:", JSON.stringify(res))
			})
		},

		//开启震动
		bvibrate() {
			my.vibrate();
		},
		// 权限引导  封装函数
		getAuthGuide(authType) {
			console.log("authType", authType)
			my.showAuthGuide({
				authType: authType,
				success: res => {
					//shown为true时表示会显示权限引导弹窗，为false时表示用户已经授权
					my.alert({
						content: '调用成功：' + JSON.stringify(res),
					});
				},
				fail: error => {
					my.alert({
						content: '调用失败：' + JSON.stringify(error),
					});
				}
			});
		},
		//扫码
		scan() {
			console.log("扫码辣")
			my.scan({
				scanType: ['qrCode', 'barCode'],
				success: (res) => {
					my.alert({
						title: res.code
					});
				},
			});
		},
		// 获取会员用户收货地址
		getAddress(){
			my.getAddress({
			  success: (res) => {
			    my.alert({
			      title: JSON.stringify(res)
			    });
			  }
			});
		}
	}
}
